import { NVector3 } from '../../../../lib/NJSCore';

export class Graph {

    nodes: Node[] = [];
    edges: Edge[] = [];
    dt: number = 0.0;

    mouse: NVector3 = new NVector3(0, 0);

    damping = 0.96;
    gravity = -0.032;
    timeStep = 0.16; // 0.316; // 0.18; // 0.26;
    groundZ = -9.26;

    capture: Node | undefined = undefined;

    constructor() {
    }
    Init(width: number, height: number) {
        this.InitGridR(width, height);
        this.nodes[0].fixed = true;
        this.nodes[9].fixed = true;
        this.nodes[this.nodes.length - 1].fixed = true;
        this.nodes[this.nodes.length - 1 - 9].fixed = true;
    }
    updateOptionA(v: number) {
        this.damping = v * 0.1;
        console.log(this.damping, 'damping');
    }
    updateOptionB(v: number) {
        this.timeStep = v * 0.05;
        console.log(this.timeStep, 'timeStep');
    }
    updateOptionC(v: number) {
        // this.gravity = v * 0.1;
        // console.log(this.gravity, 'gravity');
    }
    shrinkage(id: number, v: number) {
        this.nodes[id].fixed = true;
        for (let i = 0; i < this.nodes[id].edge.length; ++i) {
            this.nodes[id].edge[i].targetLengthShrinkage(v)
        }
    }
    InitGridR(width: number, height: number, xNum: number = 10, yNum: number = 10) {
        const xOff = 0;
        const yOff = 0;
        const xInterval = (width / xNum) + 1;
        const yInterval = (height / yNum) + 1;
        const nbase = this.nodes.length;
        for (let i = 0; i < yNum; ++i) {
            for (let j = 0; j < xNum; ++j) {
                let x = xOff + xInterval * j - width * 0.5;
                let y = yOff + yInterval * i - height * 0.5;
                this.AddNode(x, 0, y, NVector3.Zero(), 1.0);
                // this.nodes.push(new Node(new NVector3(x, y), new NVector3(0, 0), 1));
            }
        }
        for (let j = 0; j < yNum; ++j) {
            for (let i = 0; i < xNum; ++i) {
                let k = j * xNum + i;

                if (i !== 0) { this.AddEdge(this.nodes[nbase + k - 1], this.nodes[nbase + k]); }
                if (j !== 0) { this.AddEdge(this.nodes[nbase + k - xNum], this.nodes[nbase + k]); }

                // if (i !== 0 && j !== 0) {this.AddEdge(this.nodes[nbase + k - xNum - 1], this.nodes[nbase + k]); }

                // if (i !== 0 && j !== yNum - 1) {this.AddEdge(this.nodes[nbase + k + xNum - 1], this.nodes[nbase + k]); }
            }
        }
    }
    public Clear() {
        this.nodes = [];
        this.edges = [];
    }
    public AddNode(x: number, y: number, z: number, _u0: NVector3 = new NVector3(0, 0, 0), _mass: number = 1): Node {
        const n = new Node(new NVector3(x, y, z), _u0, _mass);
        this.nodes.push(n);
        n.id = this.nodes.length - 1;
        return n;
    }
    public AddEdge(n0: Node, n1: Node): Edge {
        const e = new Edge(n0, n1);
        n0.edge.push(e);
        n1.edge.push(e);
        this.edges.push(e);
        return e;
    }
    MouseMove(x: number, y: number) {
        this.mouse.x = x;
        this.mouse.y = y;
        console.log('move', x, y);
    }
    MouseDClick(x: number, y: number) {
        this.mouse.x = x;
        this.mouse.y = y;
        const n = this.FindNode(this.mouse);
        if (n) {
            n.fixed = !n.fixed;
        }
        console.log('click', x, y);
    }
    MouseDown(x: number, y: number) {
        this.mouse.x = x;
        this.mouse.y = y;
        this.capture = this.FindNode(this.mouse);
        if (this.capture) {
            this.capture.fixed = true;
            this.capture.isCapture = true;
        }
        console.log('down', x, y, this.capture);
    }
    MouseLeftClick(x: number, y: number) {
        console.log('click', x, y, this.capture);
    }
    MouseUp(x: number, y: number) {
        this.mouse.x = x;
        this.mouse.y = y;
        if (this.capture) {
            this.capture.isCapture = false;
            this.capture = undefined;
        }
        console.log('up', x, y);
    }
    MouseDrag(x: number, y: number) {
        this.mouse.x = x;
        this.mouse.y = y;
        if (this.capture) {
            this.capture.v.x = x;
            this.capture.v.y = y;
        }
        console.log('drag', x, y);
    }
    Render(ctx?: CanvasRenderingContext2D) {

        this.dt += 0.1;
        this.Move(this.mouse.x, this.mouse.y, this.mouse.z, this.damping, 0, this.timeStep, this.groundZ, 1);

        // for (let e of this.edges) { e.Render(ctx); }
        // for (let n of this.nodes) { n.Render(ctx, this.mouse); }
    }
    FindNode(mv: NVector3) {
        for (let n of this.nodes) {
            if (n.v.Distance(mv) < n.m + 3) {
                return n;
            }
        }
        return undefined;
    }
    public Move(x: number, y: number, z: number, damping: number, g: number = 0, dt: number = 0.01, groundZ: number = -10, step: number = 1) {
        const atr = new NVector3(x, y, z);
        for (let i = 0; i < step; ++i) {
            for (const n of this.nodes) {

                // const dv: NVector3 = atr - n.p;
                //  L = dv.Length;
                // dv.Unitize();
                // n.f = dv * Math.Exp(-0.1 * L * L) * 0.1 + Vector3d.ZAxis * g;

                const dv = NVector3.Sub(atr, n.v);
                const L = dv.Length();
                dv.Unitize();
                n.f = NVector3.Add(NVector3.Scale(dv, Math.exp(-0.1 * L * L) * 0.1), (NVector3.Scale(NVector3.YAxis(), g)));
            }
            for (const e of this.edges) { e.ApplySpringForce(); }
            for (const n of this.nodes) { n.Move(dt, damping, groundZ); }
        }
    }
}
export class Edge {
    n0: Node;
    n1: Node;
    public targetLength: number = 0.0;
    public isModified = false;
    public springConstant: number = 0.8; // 1; // 0.8;
    private _targetLengthOriginal = 0;
    constructor(n0: Node, n1: Node) {
        this.n0 = n0;
        this.n1 = n1;
        this.targetLength = this.n0.v.Distance(this.n1.v) * 0.95;
        this._targetLengthOriginal = this.targetLength;
    }
    Render(ctx: CanvasRenderingContext2D) {
        ctx.strokeStyle = '#ff0000';

        if (NVector3.Distance(this.n0.v, this.n1.v) < this.targetLength * 2) {

            ctx.strokeStyle = '#0000ff';
            // this.n0.isShort = true;
            // this.n1.isShort = true;
        }

        ctx.beginPath();
        ctx.moveTo(this.n0.v.x, this.n0.v.y);
        ctx.lineTo(this.n1.v.x, this.n1.v.y);
        ctx.closePath();
        ctx.stroke();
    }
    targetLengthShrinkage(n: number) {
        if (!this.isModified) {
            this.isModified = true;
            this.targetLength *= n;
        }
    }

    public ApplySpringForce() {
        // Vector3d dp = n1.p - n0.p;
        // double dist = dp.Length;
        // dp.Unitize();

        // n0.f += dp * (dist - TargetLength) * SpringConstant;
        // n1.f -= dp * (dist - TargetLength) * SpringConstant;

        // const dp: NVector3 = NVector3.Sub(this.n1.p, this.n0.p);
        // const dist:number = dp.Length();
        // dp.Unitize();

        let dp = NVector3.Sub(this.n1.v, this.n0.v);
        let dist = dp.Length();
        dp.Unitize();

        const length = NVector3.Scale(dp, (dist - this.targetLength) * this.springConstant);
        this.n0.f = NVector3.Add(this.n0.f, length);
        this.n1.f = NVector3.Sub(this.n1.f, length);

    }
}
export class Node {
    public id: number = -1;
    public edge: Edge[] = [];
    public nNodes: Node[] = [];
    public v: NVector3 = NVector3.Origin();
    public f: NVector3 = NVector3.Origin();
    public u: NVector3 = NVector3.Origin();
    public m: number = 0.0;

    public fixed: boolean = false;
    public isCapture: boolean = false;
    constructor(_p0: NVector3, _u0: NVector3, _mass: number) {
        this.v = new NVector3(_p0.x, _p0.y, _p0.z);
        this.u = new NVector3(_u0.x, _u0.y, _u0.z);
        this.m = _mass;
    }
    public Move(dt: number, damping: number, groundZ: number = 0.0) {
        if (this.fixed || this.isCapture) { return; }

        // u *= damping;
        // u += f * (dt / m);
        // p += u * dt;

        // this.u = NVector3.Scale(this.u , damping );
        // this.u = NVector3.Add( this.u,  NVector3.Scale(this.f, dt / this.m));
        // this.p  = NVector3.Add(this.p, NVector3.Scale(this.u, dt));

        this.u.Mult(damping);
        this.u.Add(NVector3.Scale(this.f, dt / this.m));

        const v = NVector3.Scale(this.u, dt);
        // this.v.Add( );
        this.v.x += v.x;
        this.v.y += v.y;

        // if (this.p.y < groundZ) {
        //     this.p.y = groundZ;
        //     if (this.u.y < 0.0) {this.u.y = -this.u.y; }
        // }
    }
    Render(ctx: CanvasRenderingContext2D, m: NVector3) {
        ctx.beginPath();

        if (m.Distance(this.v) < this.m + 3) {
            ctx.fillStyle = '#000000';
            ctx.arc(this.v.x, this.v.y, this.m + 6, 0.0, Math.PI * 2);
        } else {
            ctx.fillStyle = '#f90000';
            ctx.arc(this.v.x, this.v.y, this.m + 3, 0.0, Math.PI * 2);
        }

        if (this.fixed) {
            ctx.fillStyle = '#000000';
            ctx.arc(this.v.x, this.v.y, this.m + 7.5, 0.0, Math.PI * 2);
        }

        ctx.closePath();
        ctx.fill();
    }
}