import * as THREE from 'three';
import { RendererThree, getCSVFromURL, getJSONFromURL } from '../../../../lib';
import { NVector3, } from '../../../../lib/NJSCore';
import { UI } from './ui';
import { Graph } from './Graph';

export class Solution extends RendererThree {

    pointLight: THREE.PointLight;
    pointer = new THREE.Vector2();
    raycaster = new THREE.Raycaster();
    sphere: THREE.Mesh;

    lines: THREE.LineSegments[] = [];
    linesIndex: number[] = [];

    mesh: THREE.Points;

    lookupClass = [];
    lookupTable = {};

    graph: Graph;

    needInterpolation: boolean = true;

    preSelLine: THREE.LineSegments;

    ui: UI;

    idLookupTable;

    cameraAni = new THREE.Vector3(0, 100, 100);

    constructor(id: string = 'main') {
        super(id)

        this.ui = new UI(this);

        this.camera.position.set(0, 500, 500);

        const ambientLight = new THREE.AmbientLight(0xcccccc, 0.25);
        this.scene.add(ambientLight);

        this.pointLight = new THREE.PointLight(0xffffff, 0.75);
        this.pointLight.position.x = 10;
        this.pointLight.castShadow = true;
        this.scene.add(this.pointLight);

        this.raycaster = new THREE.Raycaster();
        this.raycaster.params.Points.threshold = 0.4;
        this.raycaster.far = 1000;

        const sphereGeometry = new THREE.SphereGeometry(0.1, 32, 32);
        const sphereMaterial = new THREE.MeshBasicMaterial({ color: 0xffffff });
        const sphere = new THREE.Mesh(sphereGeometry, sphereMaterial);
        this.sphere = sphere;
        this.scene.add(sphere);

        this.graph = new Graph();

        getCSVFromURL(process.env.PUBLIC_URL + '/static/data/smDataViz.csv').then((d) => {
            const line = d.split(/\r\n|\r|\n/g);

            const vertices = [];
            const color = [];


            const lookupClass = []
            const lookupTable = {}


            for (let i = 0; i < line.length; ++i) {

                const pos = line[i].split(',')


                const x = +pos[0] * 0.1;
                const y = +pos[1] * 0.1;
                const z = +pos[2] * 0.1;
                vertices.push(x, y, z);

                this.graph.AddNode(x, y, z);

                color.push(0.5, 0.5, 0.5);

                const classN = pos[3];
                lookupClass.push(classN)

                const neighbor = {};
                for (let j = 4; j < pos.length; j += 2) {
                    const keyword = pos[j].split('(')[1].replaceAll("'", "");
                    const prob = +pos[j + 1].split(')')[0];
                    // console.log(keyword, prob)
                    neighbor[keyword] = prob;
                }

                lookupTable[classN] = {
                    id: i,
                    pos: [x, y, z],
                    neighbor,
                }


            }


            for (let i = 0; i < lookupClass.length; ++i) {
                const neighbors = lookupTable[lookupClass[i]].neighbor;
                const n0 = this.graph.nodes[i];
                for (let key in neighbors) {
                    // console.log(key)
                    const node = lookupTable[key]
                    const n1 = this.graph.nodes[node.id];
                    this.graph.AddEdge(n0, n1);
                    // break;
                }
            }

            // console.log(this.graph)

            // console.log(lookupTable)
            this.lookupClass = lookupClass;
            this.lookupTable = lookupTable;




            const geometry = new THREE.BufferGeometry();

            geometry.setAttribute('position', new THREE.Float32BufferAttribute(vertices, 3));
            geometry.setAttribute('color', new THREE.Float32BufferAttribute(color, 3));
            const material = new THREE.PointsMaterial({
                // color: 0x00fff0 , 
                vertexColors: true,
                size: 2,
            });
            const points = new THREE.Points(geometry, material);
            geometry.computeBoundingSphere();
            this.scene.add(points);
            this.mesh = points;



            for (let i = 0; i < this.graph.nodes.length; ++i) {
                const e = this.graph.nodes[i].edge;

                const geometryLine = new THREE.BufferGeometry();
                // const materialLine = new THREE.LineBasicMaterial({  linewidth: 0.0001,  opacity: 0.01 });
                const materialLine = new THREE.LineDashedMaterial({ color: 0x333333, dashSize: 0.071, gapSize: 0.5, opacity: 0.0001 });

                const positions = [];
                const colorLine = [0.2, 0.0, 0.2]

                for (let j = 0; j < e.length; j++) {
                    const n0 = e[j].n0;
                    const n1 = e[j].n1;

                    positions.push(n0.v.x, n0.v.y, n0.v.z)
                    positions.push(n1.v.x, n1.v.y, n1.v.z)
                }

                // colors.push(colorLine[0], colorLine[1], colorLine[2]);
                // colors.push(colorLine[0], colorLine[1], colorLine[2]);

                geometryLine.setAttribute('position', new THREE.Float32BufferAttribute(positions, 3));

                geometryLine.computeBoundingSphere();

                const ln = new THREE.LineSegments(geometryLine, materialLine);
                ln.computeLineDistances();
                this.scene.add(ln);
                this.lines.push(ln)
            }

            getJSONFromURL(process.env.PUBLIC_URL + '/static/data/smIDLookUpTable.json').then((d2) => {
                this.idLookupTable = JSON.parse(JSON.stringify(d2));

                this.start();
            });


        });

    }
    public mouseMove(e: MouseEvent): void {
        this.pointer.x = (e.offsetX / this.hostDiv.clientWidth) * 2 - 1;
        this.pointer.y = - (e.offsetY / this.hostDiv.clientHeight) * 2 + 1;
    }
    public mouseDown(e: MouseEvent): void {
        this.pointer.x = (e.offsetX / this.hostDiv.clientWidth) * 2 - 1;
        this.pointer.y = - (e.offsetY / this.hostDiv.clientHeight) * 2 + 1;

        // this.raycaster.setFromCamera(this.pointer, this.camera);
        // const intersections = this.raycaster.intersectObjects([this.mesh], false);
        // const intersection = (intersections.length) > 0 ? intersections[0] : null;

        // if (intersection !== null) {
        //     // console.log(intersection.index);
        //     this.sphere.position.set(intersection.point.x, intersection.point.y + 1, intersection.point.z);
        //     // this.mesh.geometry.attributes.position.setXYZ(intersection.index ,intersection.point.x, intersection.point.y + 1, intersection.point.z);
        //     this.graph.shrinkage(intersection.index);
            
        //     const className = this.lookupClass[intersection.index];
        //     this.ui.updateSubID(this.idLookupTable[className])
        // }
        // // this.mesh.geometry.attributes.position.needsUpdate = true;
        // // this.mesh.geometry.attributes.color.needsUpdate = true;
        // // this.mesh.geometry.computeVertexNormals();
        // // console.log('click')

    }
    public mouseClick(e: MouseEvent): void {
        this.pointer.x = (e.offsetX / this.hostDiv.clientWidth) * 2 - 1;
        this.pointer.y = - (e.offsetY / this.hostDiv.clientHeight) * 2 + 1;

        this.raycaster.setFromCamera(this.pointer, this.camera);
        const intersections = this.raycaster.intersectObjects([this.mesh], false);
        const intersection = (intersections.length) > 0 ? intersections[0] : null;

        if (intersection !== null) {
            // console.log(intersection.index);
            this.sphere.position.set(intersection.point.x, intersection.point.y + 1, intersection.point.z);
            // this.mesh.geometry.attributes.position.setXYZ(intersection.index ,intersection.point.x, intersection.point.y + 1, intersection.point.z);
            
            const className = this.lookupClass[intersection.index];
            this.ui.updateSubID(this.idLookupTable[className])
        }
        // this.mesh.geometry.attributes.position.needsUpdate = true;
        // this.mesh.geometry.attributes.color.needsUpdate = true;
        // this.mesh.geometry.computeVertexNormals();
        // console.log('click')
    }
    public update(scene: THREE.Scene) {
        // this.graph.Move(this.sphere.position.x, this.sphere.position.y, this.sphere.position.z, 0.999, 0, 0.0001);
        this.graph.Render();

        let numVertices = this.mesh.geometry.attributes.position.count;
        for (let i = 0; i < numVertices; i++) {
            let o = this.mesh.geometry.attributes.position;
            // let x = o.getX(i);
            // let y = o.getY(i);
            // let z = o.getZ(i)
            let x = this.graph.nodes[i].v.x;
            let y = this.graph.nodes[i].v.y;
            let z = this.graph.nodes[i].v.z;

            // y = Math.sin(9 * x * z + this.t * 5)
            // y = y < 0 ? 0 : y;
            // let p = new THREE.Vector3(x, y, z);

            this.mesh.geometry.attributes.position.setXYZ(i, x, y, z);
            // this.mesh.geometry.attributes.size.
            this.mesh.geometry.attributes.color.setXYZ(i, 0.3, 0.3, 0.3);
        }

        this.raycaster.setFromCamera(this.pointer, this.camera);
        const intersections = this.raycaster.intersectObjects([this.mesh], false);
        const intersection = (intersections.length) > 0 ? intersections[0] : null;



        if (intersection !== null) {
            // console.log(intersection.index);
            this.sphere.position.set(intersection.point.x, intersection.point.y, intersection.point.z);

            const className = this.lookupClass[intersection.index];
            const data = this.lookupTable[className];
            // console.log(className, data);

            this.ui.divTitle.textContent = 'Keyword: ' + className;
            this.ui.updateSub(data.neighbor);
            this.graph.shrinkage(intersection.index, 0.6);
            this.mesh.geometry.attributes.color.setXYZ(intersection.index, 0, 1, 1);

            for (let key in data.neighbor) {
                const d = this.lookupTable[key]; 
                // console.log(d, key);
                this.mesh.geometry.attributes.color.setXYZ(d.id, 1, 1, 0);
            }

        }
        this.mesh.geometry.attributes.position.needsUpdate = true;
        this.mesh.geometry.attributes.color.needsUpdate = true;
        this.mesh.geometry.computeVertexNormals();





        for (let i = 0; i < this.graph.nodes.length; ++i) {
            const e = this.graph.nodes[i].edge;
            const ln: THREE.LineSegments = this.lines[i];
            const positions = [];
            for (let j = 0; j < e.length; j++) {
                const n0 = e[j].n0;
                const n1 = e[j].n1;

                positions.push(n0.v.x, n0.v.y, n0.v.z)
                positions.push(n1.v.x, n1.v.y, n1.v.z)

            }

            let count = 0;
            for (let j = 0; j < positions.length; j += 6) {

                ln.geometry.attributes.position.setXYZ(count++, positions[j], positions[j + 1], positions[j + 2])
                ln.geometry.attributes.position.setXYZ(count++, positions[j + 3], positions[j + 4], positions[j + 5]);
            }
            // colors.push(colorLine[0], colorLine[1], colorLine[2]);
            // colors.push(colorLine[0], colorLine[1], colorLine[2]);
            ln.geometry.attributes.position.needsUpdate = true;

        }

        if (intersection !== null) {
            if (this.preSelLine !== undefined) {
                const mat = this.preSelLine.material as THREE.LineDashedMaterial;
                mat.color.setRGB(0.3, 0.3, 0.3);
                this.preSelLine = undefined;
            }

            const id = intersection.index;
            const mat = this.lines[id].material as THREE.LineDashedMaterial;
            mat.linewidth = 1;
            mat.dashSize = 0.3;
            this.lines[id].computeLineDistances();
            mat.color.setRGB(0, 1, 1);

            this.preSelLine = this.lines[id];

        } else {
            if (this.preSelLine !== undefined) {
                const mat = this.preSelLine.material as THREE.LineDashedMaterial;

                mat.dashSize = 0.071
                mat.linewidth = 0.1;
                this.preSelLine.computeLineDistances();
                mat.color.setRGB(0.3, 0.3, 0.3);
                this.preSelLine = undefined;
            }

        }

        this.CameraInterpolation();


    }
    CameraInterpolation() {
        if (this.needInterpolation) {

            const v = easingMotion(this.camera.position, this.cameraAni)
            this.camera.position.set(v.x, v.y, v.z);

            // const v2 = easingMotion(this.controls.target, this.cameraAni)
            // this.controls.target.set(v2.x, v2.y, v2.z);

            if (this.camera.position.distanceTo(this.cameraAni) < 1) {
                this.needInterpolation = false;
            }
        }
    }
}


export const easingMotion = (v1: THREE.Vector3, v2: THREE.Vector3, speed: number = 0.15) => {
    const v = new THREE.Vector3(v2.x - v1.x, v2.y - v1.y, v2.z - v1.z)
    return new THREE.Vector3(
        v1.x + v.x * speed,
        v1.y + v.y * speed,
        v1.z + v.z * speed,
    );
};